import { Component, EventEmitter, Output } from '@angular/core';

import { DataService } from '../service/data.service';
import { SnapshotEntity } from '../service/entity/snapshot.entity';
import { TabsEvent } from '../tabs/tabs.event';

@Component({
  templateUrl: 'history.html'
})
export class HistoryPage {

  items: SnapshotEntity[];

  // @Output()
  // onItemSelected = new EventEmitter<SnapshotEntity>();

  constructor(private dataServ: DataService, private tabsEvent: TabsEvent) {
    this.items = this.dataServ.getSnapshotList(6, 0);
  }

  itemSelected(item) {
    this.tabsEvent.histtoryItemClicked(item);
    // this.onItemSelected.emit(item);
  }
}


