import { Injectable } from '@angular/core';
import { Subject }    from 'rxjs/Subject';
import { SnapshotEntity } from '../service/entity/snapshot.entity';
 
@Injectable()
export class TabsEvent {
 
  // Observable string sources
  private histtoryItemSource = new Subject<SnapshotEntity>();
 
  // Observable string streams
  histtoryItemSource$ = this.histtoryItemSource.asObservable();
 
  // Service message commands
  histtoryItemClicked(item: SnapshotEntity) {
    this.histtoryItemSource.next(item);
  }
}


